import jwtDecode from 'jwt-decode';
import cookies from 'js-cookie';
import axios from '~global-js/helpers/axios';
import { Base64 as base64 } from 'js-base64';
import cookieSettings from '~global/variables/cookies.json';
import customEvents from '~global/variables/custom-events.json';
import { getCookieDomain, getGraphQlDomain } from '~global-js/helpers/environment';
import fireCustomEvent from '~global-js/helpers/custom-events';
import { setItem, getItem, removeItem } from '~global-js/helpers/storage';
import generatePath from '~global-js/helpers/router';

export function getAuthToken() {
  if (typeof document === 'undefined') {
    return null;
  }

  try {
    return cookies.get(cookieSettings.auth.name);
  } catch (error) {
    logoutUser();
    return null;
  }
}

export const authCookieProperties = {
  path: cookieSettings.auth.path,
  domain: getCookieDomain(),
};

let updateUserFailedCount = 0;
export function updateUserInfo() {
  const token = getAuthToken();
  if (!token) {
    return;
  }
  const userInfo = decodeUserInfo(token);

  if (userInfo.roles && userInfo.roles.indexOf('partner') !== -1) {
    getPartnerInfo(token);
  } else {
    axios.get(generatePath('get-user', { uuid: userInfo.uuid }), {}).then((response) => {
      window.hurricane = window.hurricane || {};
      window.hurricane.user = response.data;
      setItem('user', response.data, 'session');
      // emmit global event
      fireCustomEvent(customEvents.receivedUser.name, {
        user: response.data,
      });
    }).catch(() => {
      if (updateUserFailedCount >= 5) {
        return;
      }
      updateUserFailedCount += 1;
      // for somereason(like platform sync delay) we need to try request again
      setTimeout(() => {
        updateUserInfo();
      }, 1000);
    });
  }
}

export function getUserInfo() {
  return (window.hurricane && window.hurricane.user) || getItem('user', 'session') || null;
}

export function removeUserInfo() {
  removeItem('user', 'session');
  if (window.hurricane && window.hurricane.user) {
    delete window.hurricane.user;
  }
}

export function loginUser(token) {
  if (typeof document === 'undefined') {
    return null;
  }

  cookies.set(
    cookieSettings.auth.name,
    token,
    authCookieProperties,
  );
  triggerLoginEvent(token);
  updateUserInfo();
  return true;
}

export function logoutUser() {
  if (typeof document === 'undefined') {
    return null;
  }

  cookies.remove(
    cookieSettings.auth.name,
    authCookieProperties,
  );

  cookies.remove(
    cookieSettings.partnerInfo.name,
    {
      path: '/',
      domain: getCookieDomain(),
    }
  );
  triggerLogoutEvent();

  removeUserInfo();
  // force a reload to clear any states
  window.location.reload();
  return true;
}

export function triggerLoginEvent(token) {
  if (typeof document !== 'undefined') {
    fireCustomEvent(customEvents.userLogin.name, {
      token,
    });
  }
}

export function triggerLogoutEvent() {
  if (typeof document !== 'undefined') {
    fireCustomEvent(customEvents.userLogout.name);
  }
}

/**
 * Example decoded of a decoded jwt (public part):
 *
 * {
 *   uuid: eb27c340-f61e-477a-915c-fd533e1eba1f,
 * }
 */
export function decodeUserInfo(token) {
  if (typeof token !== 'string') {
    return null;
  }

  const tokenContents = jwtDecode(token);

  return {
    uuid: tokenContents.uuid,
    roles: tokenContents.roles || [],
    email: tokenContents.email,
  };
}

function getPartnerInfo(token) {
  axios({
    method: 'post',
    url: getGraphQlDomain(),
    data: {
      operationName: 'PP_GetBranches',
      query: `
        query PP_GetBranches {
        viewer {
          ... on PartnerContact {
            emailAddress
            firstName
            lastName
            company {
              code
              name
            }
          }
        }
      }`,
    },
    headers: {
      Authorization: `Bearer ${token}`,
    },
  }).then((response) => {
    const payload = response.data.data.viewer;
    cookies.set(
      cookieSettings.partnerInfo.name,
      base64.encodeURI(JSON.stringify({
        partnerName: payload.company.name,
        code: payload.company.code,
        email_address: payload.emailAddress,
        name: `${payload.firstName} ${payload.lastName}`,
      })),
      {
        path: '/',
        domain: getCookieDomain(),
      },
    );
    fireCustomEvent('hideButtonsForPartner');

    if (typeof document !== 'undefined') {
      fireCustomEvent(customEvents.partnerCookieSet.name, {
        data: payload,
      });
      fireCustomEvent(customEvents.receivedUser.name);
    }
  });
}



