import { getLocale, defaultLocale } from '~global-js/helpers/locale';
import routes from '~global/variables/routing.json';

const cache = {};
const nonLocalisedKeys = Object.keys(routes.nonLocalised.childRoutes);

/**
 * @param  {Object} parentRoute
 * @param  {String} childRouteName
 *
 * @return {Object}
 */
function getPathFragment(parentRoute, childRouteName) {
  if (parentRoute.childRoutes[childRouteName]) {
    return {
      routeObject: parentRoute.childRoutes[childRouteName],
      path: parentRoute.childRoutes[childRouteName].path,
    };
  }

  throw new Error('generatePath: Path not found. Please check path exists');
}

/**
 * @param  {String} path
 * @param  {String} currentRoute
 *
 * @return {String}
 */
function getPathDivider(path, currentRoute) {
  if (path.charAt(path.length - 1) === '/' || currentRoute.charAt(0) === '/') {
    return '';
  }

  return '/';
}

/**
 * Generate a route from a path
 *
 * @param  {String} name   dot-separated string e.g. 'home', 'my-account.my-applications'
 * @param  {Object} params
 * @param  {String} locale
 * @return {String}
 *
 * Eg.
 *   generatePath('property_detail', {
 *     countrySlug: 'au',
 *     citySlug: 'sydney',
 *     propertySlug: 'kyles-house'
 *   });
 *
 * Eg. generatePath('my-account.my-applications');
 *
 */
export default function generatePath(name, params, locale = getLocale()) {
  if (typeof name !== 'string') {
    throw new Error('generatePath: name must be a dot-separated string');
  }

  const cacheKey = getCacheKey(name, locale);
  let route;

  if (cache[cacheKey]) {
    route = cache[cacheKey];
  } else {
    route = buildAndCacheRoute(name, locale);
  }

  if (params) {
    Object.keys(params).forEach((key) => {
      route = route.replace(`{${key}}`, params[key]);
    });
  }

  return route;
}

function getCacheKey(routeName, locale) {
  return `${locale}.${routeName}`;
}

function buildAndCacheRoute(name, locale) {
  const routeParts = name.split('.');
  const cacheKey = getCacheKey(name, locale);
  const isDefaultLocale = locale === defaultLocale || locale === 'zh-cn';
  let route;
  let routeObject;
  let isUnlocalizedRoute;
  let isLocaleSpecificRoute;
  let routeLocale;

  if (nonLocalisedKeys.indexOf(routeParts[0]) !== -1) {
    isUnlocalizedRoute = true;
    routeObject = routes.nonLocalised;
  } else {
    isUnlocalizedRoute = false;
    routeObject = routes[locale];

    if (!routeObject) {
      throw new Error(`generatePath: route with locale "${locale}" does not exist`);
    }
  }

  route = routeObject.path;

  for (let i = 0; i < routeParts.length; i++) {
    const currentRoute = getPathFragment(routeObject, routeParts[i]);
    const pathDivider = getPathDivider(route, currentRoute.path);
    route += `${pathDivider}${currentRoute.path}`;
    routeObject = currentRoute.routeObject;
    if (routeObject.localeSpecific) {
      isLocaleSpecificRoute = true;
      routeLocale = routeObject.localeSpecific;
    }
  }


  if (isLocaleSpecificRoute && routeLocale !== defaultLocale) {
    // append locale to start of route (if not en-us) defined by the route
    route = `${routeLocale}${route}`;
  } else if (!isLocaleSpecificRoute && isUnlocalizedRoute && !isDefaultLocale) {
    // add given locale to start of url in the event that the route is made of non localized slugs
    // and is not locale specific
    route = `${locale}${route}`;
  }

  // add starting '/' if it does not exist
  if (route.charAt(0) !== '/') {
    route = `/${route}`;
  }

  cache[cacheKey] = route;

  return cache[cacheKey];
}



